:root {
  --primary-font-family: "DM Sans";
  --alternative-font-family: "Titan One";
  --accent-text-color: #d41443;
  --dominant-text-color: #fff;
  --alternative-text-color: #907e82;
  --usual-text-color: #000;
  --accent-backgroung-color: #ffb8ca;
  --title-accent-color: #ffa5ba;
  --slider-color: #d31342;
  --inactive-slider-color: #d9d9d9;
  --agency-color: #f0d1a5;
  --alternative-agency-color: #c2e297;
  --quotes-color: #e1e1e1;
  --point-review-color: #c7133e;
  --sosials-inactive-color: #fea5bb;
  --backgroung-color: #fff5f6;
  --border-mobile-menu-color: #e18298;
  --background-mobile-menu-color: #e17992;
}

body {
  font-family: var(--primary-font-family), Verdana, Tahoma, arial, sans-serif;
  background-color: var(--dominant-text-color);
  color: var(--usual-text-color);
  margin: 0;
  font-weight: 400;
}

body.modal-open {
  overflow: hidden;
}

button {
  font: inherit;
  color: inherit;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.title-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 99;
  background: rgba(227, 129, 236, .3);
  transition: opacity .2s linear;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .mobile-menu {
  transform: translate(100%);
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.header {
